/********Confirmation Box CSS : START**************/

#confirmBoxPopupId .button {
  font-size: 1em;
  padding: 10px;
  color: #000;
  border: 2px solid #06d85f;
  border-radius: 20px/50px;
  text-decoration: none;
  cursor: pointer;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  display: none;
}
#confirmBoxPopupId .button:hover {
  background: #06d85f;
}

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  -webkit-transition: opacity 500ms;
  transition: opacity 500ms;
  visibility: hidden;
  opacity: 1;
}
.overlay:target {
  visibility: visible;
  opacity: 1;
}

#confirmBoxPopupId .popup {
  margin: 90px auto;
  padding: 20px;
  background: #fff;
  border-radius: 5px;
  width: 30%;
  position: relative;
  -webkit-transition: all 5s ease-in-out;
  transition: all 5s ease-in-out;
  overflow: hidden;
}

#confirmBoxPopupId .popup .close {
  position: absolute;
  top: 0px;
  right: 6px;
  -webkit-transition: all 200ms;
  transition: all 200ms;
  font-size: 30px;
  font-weight: bold;
  text-decoration: none;
  color: #333;
}
#confirmBoxPopupId .popup .close:hover {
  color: #e76124;
}
#confirmBoxPopupId .popup .content {
  max-height: 30%;
  overflow: auto;
  padding: 18px 0;
  color: black;
  font-family: Graphik Medium;
  font-size: 16px;
  font-weight: 500;
}
#confirmBoxPopupId #buttons {
  float: right;
}
#confirmBoxPopupId #cancelBtn,
#confirmBoxPopupId #yesBtn {
  padding: 10px;
  background: linear-gradient(
    136.64deg,
    #e76124 0%,
    #f19411 100%,
    #f19411 100%
  );
  border: none;

  border-radius: 4.54px;
  color: #ffffff;
  font-family: Graphik Medium;
  font-size: 14px;
  font-weight: 500;
  line-height: 22px;
  border: none;
  margin: 5px;
  text-transform: uppercase;
  padding: 5px 20px;
}
#confirmBoxPopupId #cancelBtn {
  background: #9b9b9b;
}
#confirmBoxPopupId #cancelBtn:hover {
  color: #fff;
}
#confirmBoxPopupId #yesBtn:hover {
  color: #fff;
}
/********Confirmation Box CSS : END**************/

